.ux-paragraph-add-more {
  background: #1a1a1a;
}

.ux-paragraph-add-more .uxAside-inner {
  display: flex;
  flex-wrap: wrap;
}

.ux-paragraph-add-more .ux-button {
  flex: 0 0 20%;
  display: block;
  color: #fff;
  padding: 1.875rem;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: background 350ms;
  box-sizing: border-box;
}

.ux-paragraph-add-more .ux-button .micon {
  display: block;
  font-size: 2.5rem;
  padding-bottom: 0.625rem;
}

.ux-paragraph-add-more .ux-button:hover {
  background: #333333;
}
